/* ---------------------Menu Icon-------------------- */
.App .menu-icon-container {
  position: absolute;
  display: block;
  top: 10px;
  left: 10px;
  cursor: pointer;
  z-index: 2;
}

#menuToggle {
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-transition: -webkit-transform 0.5s ease;
  transition: -webkit-transform 0.5s ease;
  -o-transition: transform 0.5s ease;
  transition: transform 0.5s ease;
  transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}

#menuToggle span {
  display: block;
  width: 25px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;

  background: #667292;
  border-radius: 3px;

  -webkit-transform-origin: 4px 0px;

      -ms-transform-origin: 4px 0px;

          transform-origin: 4px 0px;

  -webkit-transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease,
    -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);

  transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease,
    -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);

  -o-transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;

  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;

  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
    background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease,
    -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

#menuToggle span:first-child {
  -webkit-transform-origin: 0% 0%;
      -ms-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
}

#menuToggle span:nth-last-child(2) {
  -webkit-transform-origin: 0% 100%;
      -ms-transform-origin: 0% 100%;
          transform-origin: 0% 100%;
}

/* Transform the shape to an X */

.x-icon #menuToggle {
  -webkit-transform: translate(160px);
      -ms-transform: translate(160px);
          transform: translate(160px);
}

.x-icon #menuToggle span {
  opacity: 1;
  -webkit-transform: rotate(45deg) translate(-2px, -1px);
      -ms-transform: rotate(45deg) translate(-2px, -1px);
          transform: rotate(45deg) translate(-2px, -1px);
  background: #667292;
}

.x-icon #menuToggle span:nth-last-child(2) {
  opacity: 0;
  -webkit-transform: rotate(0deg) scale(0.2, 0.2);
      -ms-transform: rotate(0deg) scale(0.2, 0.2);
          transform: rotate(0deg) scale(0.2, 0.2);
}

.x-icon #menuToggle span:nth-last-child(1) {
  -webkit-transform: rotate(-45deg) translate(0, -5px);
      -ms-transform: rotate(-45deg) translate(0, -5px);
          transform: rotate(-45deg) translate(0, -5px);
}

.x-icon #menuToggle span:nth-last-child(3) {
  -webkit-transform: rotate(45deg) translate(0, -1px);
      -ms-transform: rotate(45deg) translate(0, -1px);
          transform: rotate(45deg) translate(0, -1px);
}

